@use 'base/variables' as v;
@use 'base/mixins' as m;
@media (prefers-color-scheme: dark) {
    $grisOscuro: #2c2c3b;
    $negroClaro: #101013;
    body{
        background-color: v.$negro;
    }
    .tagline,
     .formulario label,
     .login .acciones a,
     .crear .acciones a,
     .olvide .acciones a {
        color: v.$blanco;
    }
    .descripcion-pagina, .nombre-pagina, p, label{ 
        color: v.$grisClaro;

    }
    .barra-mobile, .barra, .filtros{
        background-color: darken($color: v.$grisOscuro, $amount: 5%);
        
    }
    .barra{
        p span{
            color: v.$naranja;
        }
        .cerrar-sesion{
            @include m.boton(v.$naranja, v.$blanco);
            font-size: 1.2rem;
        }
    }
    
    .sidebar{
        @include m.tablet{
            background-color: darken($grisOscuro , 5%);
        }
        .sidebar-nav a{
            &.activo, &:hover{
                background-color: v.$naranja;
            }
        }
    }
    .dashboard{
        background-color: v.$negro;
        .enlace{
            color: v.$grisClaro; 
            &:hover{
                color: lighten($color: v.$grisClaro, $amount: 10%);
            }
        }
        .filtros{
            h2{
                color: v.$grisClaro;
            }
        }
        
        
    }
    .listado-proyectos{
        .proyecto{
            background-color: $grisOscuro;

        }
    }
    .modal{
        .formulario {
        background-color: $grisOscuro;
        legend{
            color: v.$grisClaro;
        }
    }
    }
    
    
}