@use 'base/variables' as v;
@use 'base/mixins' as m;
.barra{
    display: none;
    background-color: v.$blanco;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, .4);
    @include m.tablet{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    p{
        margin: 0;
        font-size: 1.4rem;
        span{
            color: v.$indigo;
        }
    }
    .cerrar-sesion{ 
        @include m.boton(darken(v.$indigo, 20%), v.$blanco);
        font-size: 1.2rem;
        padding: 1.5rem;
        border-radius: .5rem;
    }
}
.barra-mobile{
    background-color: v.$indigo;
    padding: 2rem 4rem ;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include m.tablet{
        display: none;
    }
    h1{
        color: v.$blanco;
        margin: 0;
    }
    .menu{
        img{
            width: .8rem;
            &:hover{
                cursor: pointer;
            }

        }
    }
}