@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar{
    opacity: 0;
    height: 0;
    overflow: hidden;
    transition-property: opacity;
    transition-duration: 800ms;
    background-color: v.$indigo;
    &.ocultar{
        opacity: 0;
    }
    &.mostrar{
        padding: 2rem;
        opacity: 1;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgb(0 0 0 / .90);
        @include m.tablet{
            display: none;
           
        }
    }
    @include m.tablet{
        
        height: 100%;
        opacity: 1;
        padding: 2rem;
        background-color: v.$indigo;
        
    }
    .contenedor-sidebar{
        display: flex;
        justify-content: space-between;
        align-items: center;

        h2{
            color: v.$blanco;
            margin: 0;
            text-align: center;
        }
        .cerrar-menu{
            display: flex;
            justify-content: center;
            height: 2rem;
            width: 2rem;
            img{
                &:hover{
                    cursor: pointer;
                }
            }
            @include m.tablet{
                display: none;
            }
        }
    }
    .sidebar-nav{
        margin-top: 5rem;
        a{
            color: v.$blanco;
            display: block;
            margin-bottom: 2.5rem;
            padding: .7rem;
            transition-property: background-color;
            transition-duration: 300ms;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 1.8rem;
            text-align: center;
            &:hover, &.activo{
                background-color: darken(v.$indigo, 20%);
                border-radius: .5rem;
            }
            @include m.tablet{
                margin-bottom: 1.5rem;
                font-size: 1.3rem;
                text-align: left;
            }
        }
    }

    .cerrar-sesion-mobile{
        .cerrar-sesion{
            @include m.boton( v.$indigo, v.$blanco);
        }
        margin-top: 10rem;
        
        @include m.tablet{
            display:none;
        }
    }
}